@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@400;600&family=Mulish:wght@200;300;400;500&display=swap');

* {

  font-family: 'Exo 2', sans-serif;
  font-family: 'Mulish', sans-serif;
  margin: 0;
  padding: 0;

}

.code {
  margin-top: 5px;
  height: 57vh;
  width: 96vw;
  border-radius: 5px;
  padding: 15px;
  margin: 5px 5px !important;
}

.output {
  height: 10vh;
  width: 50vw;
  border: 1px solid black;
  padding: 15px;
  margin: 5px 5px !important;
}

.input {
  width: 40vw;
  height: 10vh;
  padding: 15px;
  padding: 15px;
  margin: 5px 5px !important;
}



/* Dashboard container */
.dashboard-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  /* align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding: 20px;
  margin: 0px;*/
  /* gap: 30px;  */
justify-content: space-evenly;

}


.right{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.left{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.dash-heading{
  font-size: 4em;
}

/* Left side (user information) */
.footer-left {
  /* Style for desktop mode */
  align-items: center;
  justify-content: center;
}

/* Right side (PieChart) */
.footer-right {
  /* Style for desktop mode */
  align-items: center;
  justify-content: center;
}

.right h1 {
  align-items: center;
}

.footer-section {
  display: flex;
  justify-content: space-between;
}

.username {
  cursor: pointer;
  font-size: 1.5em;
  color:#76ff03;
  font-weight: 600;
}


.modal-buttons {
  color: black !important;
  background-color: #53ff03ca !important ;
  margin: 0px 5px !important;
}

.Navbar-buttons :hover{
  color: #76ff03;
}

.modal-buttons:hover {
  color: white !important;
  background-color:#61b41d  !important;
}

.code-section {
display: flex !important;
align-items: center !important;
justify-content: center !important;
}

.Allcodes-container{
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;
}


@media only screen and (max-width: 767px) {

  .username {
    cursor: pointer;
    font-size: 1em;
    font-weight: 500;
  }

.code{
  width: 87vw !important; 
  /* margin-left: 35px !important;
  height: 30vh; */ 

}
.dash-heading{
  font-size: 2em;
}


.footer-section{
  display: flex;
  flex-direction: column;
}

.output{
  width: 87vw !important; 
  /* margin-left: 35px !important; */ 
}

.footer-left{
  align-items: center !important;
  justify-content: center !important;
}
.input{
  width: 87vw !important; 
  /* margin-left: 35px !important;  */
}

.dashboard-container {
  flex-direction: column;
}

.right{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}


.listitems{
  margin-left: 20px;
}
}